import Link from "next/link";
import styles from "./styles.module.css";

export default function Header() {
  return (
    <header className={styles.header}>
      <div className={styles.container}>
        <Link href="#" className={styles.link}>
          Все котятки
        </Link>
        <Link href="#" className={styles.link}>
          Любимые котятки
        </Link>
      </div>
    </header>
  );
}
